.friendMessage {
  width: 462px;
  height: 77px;
  position: relative;
  top: 100px;
  left: 70px;
}

.friendImageAndText {
  display: flex;
}

.friendImageAndText img {
  width: 48px;
  height: 48px;

  margin-left: 70px;
}

.friendText {
  width: 414px;
  height: 60px;
  background: rgba(245, 247, 251, 1);
  box-shadow: 0px 1px 2px 0px rgba(29, 33, 38, 0.1);
  box-shadow: 0px 5px 20px 0px rgba(29, 33, 38, 0.03);

  margin-left: 10px;
  padding: 10px;
  border-radius: 20px;
}

.friendText:after {
  color: silver;
}

.friendName {
  font-family: Montserrat;
  font-size: 16px;
  font-weight: 600;
  line-height: 19.5px;
  text-align: left;

  margin-bottom: 5px;
}

.friendMessageText {
  font-family: Montserrat;
  font-size: 16px;
  font-weight: 400;
  line-height: 19.5px;
  text-align: left;
}

.friendTime {
  font-family: Montserrat;
  font-size: 10px;
  font-weight: 600;
  line-height: 12.19px;
  text-align: left;

  margin-left: 80px;
}
