.button {
  width: 133px;
  height: 30px;
  padding: 5px 24px 5px 24px;
  background: rgba(0, 102, 204, 1);
  color: #fff;
  border-radius: 3px;

  font-family: Montserrat;
  font-size: 14px;
  font-weight: 600;
  line-height: 20px;
}

.disabled {
  opacity: 0.5;
  color: rgba(0, 46, 92, 1);

  background: rgba(0, 77, 153, 1);
}

.secondary {
  width: 176px;
  color: rgba(0, 102, 204, 1);
  background-color: #fff;
  border: 1px solid rgba(0, 102, 204, 1);
}

.default {
  background: rgba(0, 102, 204, 1);
}

.red {
  width: 129px;
  background: rgba(204, 20, 57, 1);
}

