.message {
  width: 575px;
  height: 77px;
  position: relative;
  top: 50px;
  left: 635px;
}

.imageAndText {
  display: flex;
  flex-direction: row-reverse;
}

.imageAndText img {
  width: 48px;
  height: 48px;
}

.text {
  width: 414px;
  height: 60px;
  background: rgba(0, 102, 204, 1);
  box-shadow: 0px 1px 2px 0px rgba(29, 33, 38, 0.1);
  box-shadow: 0px 5px 20px 0px rgba(29, 33, 38, 0.03);
  border-radius: 20px;
  margin-right: 10px;
  padding: 10px;
  color: white;

  display: flex;
  flex-wrap: wrap;
  flex-direction: row-reverse;
}

.text:after {
  color: silver;
}

.name {
  font-family: Montserrat;
  font-size: 16px;
  font-weight: 600;
  line-height: 19.5px;
  text-align: left;

  padding-left: 350px;
  margin-bottom: 5px;
}

.messageText {
  font-family: Montserrat;
  font-size: 16px;
  font-weight: 400;
  line-height: 19.5px;
  text-align: left;
}

.time {
  font-family: Montserrat;
  font-size: 10px;
  font-weight: 600;
  line-height: 12.19px;
  text-align: left;

  margin-right: 10px;
  display: flex;
  flex-direction: row-reverse;
}
