.sendForm {
  display: flex;
  align-items: center;
}

.textarea {
  position: relative;
  width: 976px;
  min-height: 40px;
  top: 150px;
  left: 70px;
  border-radius: 20px;
  overflow: hidden;
  resize: none;
  background: rgba(245, 247, 251, 1);
  box-shadow: 0px 1px 2px 0px rgba(29, 33, 38, 0.1);
  box-shadow: 0px 5px 20px 0px rgba(29, 33, 38, 0.03);

  padding: 10px;
}

.button {
  position: relative;
  width: 120px;
  height: 40px;
  top: 150px;
  left: 100px;
  border-radius: 20px;
  background: rgba(0, 102, 204, 1);
}

.button:hover {
  width: 122px;
  height: 42px;
  border-radius: 2px solid rgb(127, 172, 244);
}
.button:active {
  border-radius: 1px solid rgb(127, 172, 244);
}
