.greetingForm {
  margin-left: 70px;
  margin-top: 30px;
}

.inputAndButtonContainer {
  display: flex;
}

.error {
  font-family: Montserrat;
  font-size: 14px;
  font-weight: 400;
  line-height: 17.07px;
  text-align: left;
  color: rgba(204, 0, 0, 1);
}

.input {
  width: 372px;
  height: 36px;

  opacity: 50%;
  border-radius: 5px;
  margin-right: 10px;
  border: 1px solid rgba(0, 0, 0, 0.5);
}

.input:focus {
  border: 1px solid rgba(0, 0, 0, 0.5);
}

.errorInput {
  width: 372px;
  height: 36px;
  border: 1px solid red;
  border-radius: 5px;
  margin-right: 10px;
}

.button {
  color: white;
  width: 78px;
  height: 36px;
  padding: 5px 24px 5px 24px;
  border-radius: 3px 0px 0px 0px;
  background: rgba(0, 102, 204, 1);
  opacity: 50%;
}

.button:focus {
  background: rgba(0, 102, 204, 1);
  opacity: 0%;
}

.button:active {
  background: rgba(0, 102, 204, 1);
  opacity: 0%;
}

.button:disabled {
  background: rgba(0, 102, 204, 1);
  opacity: 50%;
}

.text {
  font-family: Montserrat;
  font-size: 14px;
  font-weight: 400;
  line-height: 17.07px;
  text-align: left;
  margin-bottom: 5px;

  color: rgba(0, 0, 0, 0.5);
}

.greeting {
  font-family: Montserrat;
  font-size: 14px;
  font-weight: 500;
  line-height: 17.07px;
  text-align: left;
  color: rgba(0, 0, 0, 1);
  width: 144px;
  height: 17px;
}
